<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <form-card
        v-for="group of centerGroups"
        :group="group"
        v-model="form"
        @updateHandle="updateHandle"
      ></form-card>
      <custom-field
        v-if="customField.length > 0"
        :group="customField"
        :value="custom_field"
        v-model="custom_field"
      ></custom-field>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <form-card
        v-for="group of sideGroups"
        hasTop="true"
        :group="group"
        v-model="form"
      >
      </form-card>
      <div class="card" v-if="listView">
        <div class="card-header">
          <h6 class="title">Giao diện</h6>
        </div>
        <div class="card-body form-card">
          <div class="row">
            <div class="col-sm-12">
              <my-select
                class="full-width"
                :attribute="listView"
                placeholder="Giao diện"
                v-model="form.template"
              >
              </my-select>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hủy</el-button>
        <el-button type="primary" @click="save">Cập nhật</el-button>
      </div>
    </div>
    <el-dialog title="Nhân bản bài viết" :visible.sync="dialogFormDoubleVisible" width="30%" @keyup.enter.native="doubleItem">
      <el-form :model="formDouble">
        <el-form-item label="Tiêu đề bài viết mới:">
          <el-input v-model="formDouble.title"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormDoubleVisible = false">Đóng</el-button>
        <el-button type="primary" @click="doubleItem">Nhân bản</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import CustomField from 'src/components/UIComponents/CustomField.vue'
import { Message, Input, Button, MessageBox, Dialog, Form, FormItem } from 'element-ui';
import MySelect from 'src/components/UIComponents/Select'
import dataFrom from './article-form'
import { mapState } from 'vuex'
import util from '@/helpers/util'

let _form = {}, _custom_field = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    CustomField,
    ElInput: Input,
    ElButton: Button,
    ElDialog: Dialog,
    ElForm: Form,
    ElFormItem: FormItem,
    MySelect
  },
  beforeCreate() {
    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    if (this.$store.state.customFieldDefine.article){
      _custom_field = this.$store.state.customFieldDefine.article.reduce(function (result, item) {
        result[item.handle] = '';
        return result;
      }, {});
    }

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })

    let id = this.$route.params.id;
    this.$store.dispatch('getCustomField', {
      type: 'article',
      id: id
    });
    this.$store.dispatch('getListThemeView', 'article');

    _custom_field = {
      gallery: ''
    }
  },
  data () {
    return {
      centerGroups: firstGroups,
      sideGroups: secondGroups,
      custom_field: _custom_field,
      dialogFormDoubleVisible: false,
      formDouble: {},
      show_fields: []
    }
  },
  computed: {
    ...mapState({
      originalForm: state => state.articleDetail
    }),
    form: {
      get () {
        this.formDouble.title = 'Nhân bản của ' + this.originalForm.title;
        let hashSeo = {};
        if (this.originalForm.seo){
          hashSeo = util.hashFlatten({seo: this.originalForm.seo});
        }
        let _originalForm = Object.assign({}, this.originalForm, hashSeo)
        return _originalForm;
      },
      set (value) {}
    },
    customField: {
      get() {
        let data = this.$util.filterByHandle(this.$store.state.customField, this.show_fields);
        for (let item of data) {
          if (item.value) {
            _custom_field[item.handle] = item.value;
          } else {
            _custom_field[item.handle] = '';
          }
        }
        return data||'';
      },
      set(value) {}
    },
    listView() {
      let self = this;
      let listTheme = self.$store.state.listThemeView;
      if (listTheme.length) {
        listTheme.unshift({
          title: 'Mặc định',
          value: ''
        })
        return {
          options: listTheme
        };
      }
      return '';
    }
  },
  mounted() {
    this.getContext();
    let id = this.$route.params.id;
    this.$store.dispatch('fetchArticleDetail', id);

    this.$store.dispatch('setPageTitle', 'Cập nhật bài viết');
    this.$store.dispatch('setCurrentActions', [{
        label: 'Nhân bản',
        type: 'default',
        icon: '',
          callback: this.showDouble
      },{
        label: 'Xem trên web',
        type: 'default',
        icon: '',
        callback: this.view
      },{
        label: 'Xóa',
        type: 'warning',
        icon: '',
        callback: this.remove
      }, {
        label: 'Cập nhật',
        type: 'primary',
        icon: '',
        callback: this.save
      }
    ])

  },
  methods: {
    cancel() {
      this.$router.push({name: 'AllArticles'});
    },
    updateHandle(){
      let self = this;
      let data = {
        handle: self.$util.createHandle(self.form.title),
        lang: 'vi'
      }
      self.$store.dispatch('checkHandle', data).then(result => {
        self.$store.state.articleDetail = self.form;
        self.$store.state.articleDetail.handle = result;
      });
    },
    remove(){
      let self = this;
      MessageBox.confirm('Bạn có chắc chắn xóa bài viết không?', 'Warning', {
        confirmButtonText: 'Đồng ý',
        cancelButtonText: 'Hủy bỏ',
        type: 'warning',
        center: true
      }).then(() => {
        self.$store.dispatch('removeArticle', self.form.id).then(res => {
          Message({
            type: 'success',
            message: 'Đã xóa bài viết'
          });
          self.$router.push({name: 'AllArticles'});
        });
      }).catch(() => {
        Message({
          type: 'info',
          message: 'Hủy bỏ'
        });
      });
    },
    view(){
      let self = this;
      var win = window.open(self.form.url, '_blank');
      if (win) {
        win.focus();
      } else {
        Message({
          message: 'Please allow popups for this website',
          type: 'danger'
        });
      }
    },
    showDouble(){
      this.dialogFormDoubleVisible = true;
    },
    doubleItem(){
      let self = this;
      let data = {
        id: self.originalForm.id,
        title: self.formDouble.title
      };
      this.$store.dispatch('doubleArticle', data).then(resp => {
        self.$router.push({path: '/articles/' + resp.id});
        location.reload();
        self.dialogFormDoubleVisible = false;
      }, error => {
        Message({
          message: error.message,
          type: 'danger'
        });
      })
    },
    save() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result) {
          let blogs = [];
          if (self.form.objBlog) {
            for (var item in self.form.objBlog) {
              let blog = self.form.objBlog[item];
              blogs.push(blog.id);
            }
          }
          self.form.blogs = blogs;
          self.$store.dispatch('updateArticle', self.form).then(res => {
            self.$store.dispatch('storeCustomField', self.$util.parseDataCustomfield(self.customField, self.custom_field, 'article', self.form.id));

            Message({
              message: 'Cập nhật thành công',
              type: 'success'
            });
          }, res => {
            Message({
              message: res.message,
              type: 'danger'
            });
          });
        }
      });
    },
    getContext() {
      let ctx = this.$util.getContext(this.form, {module: 'articles'});
      this.$store.state.ctx = ctx;
      return ctx;
    },
  },
  watch: {
    'form': {
      handler(newVal, oldVal) {
        let ctx = this.getContext();
        let output = window.__FUNC.CustomField(ctx);
        this.show_fields = output;
      },
      deep: true
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
    this.$store.state.articleDetail = [];
    this.$store.state.listThemeView = [];
    this.$store.state.customField = [];
  }
}
</script>
